/* Reset default margin and padding for all elements */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
* {
    box-sizing: border-box; /* Use box-sizing for better padding and width management */
    margin: 0;
    padding: 0;
    font-family: 'Poppins';
}

/* Styles for the entire body */
html, body {
    height: 100%;
    width: 100%; /* Full height */
    font-family: Arial, sans-serif; /* Font styling */
    background-color: #f4f7f8; /* Light background color */
    color: #333; /* Default text color */
}

#root{
    display: flex;
    justify-content: center;
    align-items: center;

}
.mainBox{
    margin: 10px;
    overflow-y: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(-225deg, #FFFEFF 0%, #D7FFFE 100%);
    
}
/* Container for the login and manager forms */
.LogWin{
    height: 300px;
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
    border-radius: 20px;
}
/* Styles for the input fields */
input[type="text"],
input[type="password"] {
     /* Full width accounting for padding */
    padding: 10px; /* Input field padding */
    margin: 10px ; /* Spacing between input fields */
    border: 1px solid #6c757d; /* Light grey border */
    border-radius: 4px; /* Rounded corners */
    transition: border-color 0.3s; /* Smooth border changes */
}

/* Focus effect for input fields */
input[type="text"]:focus,
input[type="password"]:focus {
    border-color: #28a745; /* Green border when focused */
    outline: none; /* Remove default outline */
}

/* Styles for the submit button */
button {
    margin: 5px;
    padding: 10px;
    background-color: #28a745; /* Green background */
    color: white; /* White text color */
    border: none; /* No borders */
    border-radius: 10px; /* Rounded corners */
    cursor: pointer; /* Pointer cursor on hover */
    font-size: 16px; /* Font size for button */
    transition: background 0.3s ease; /* Button color transition */
}

/* Hover effect for buttons */
button:hover {
    background-color: #218838; /* Darker green on hover */
}

/* Styles for error messages */
.error-message {
    color: #e74c3c; /* Bright red for errors */
    font-size: 0.9em; /* Slightly smaller text */
    text-align: center; /* Center alignment */
}

/* Styles for tables */
table {
    width: 100%; /* Full width for tables */
    border-collapse: collapse; /* Remove space between cells */
    margin: 20px; /* Spacing above tables */
}

th, td {
    padding: 10px; /* Padding for cells */
    border: 1px solid #dddddd; /* Light grey border */
    text-align: left; /* Align text to the left */
}

th {
    background-color: #2980b9; /* Header background color */
    color: #ffffff; /* White text for headers */
}

tr:nth-child(even) {
    background-color: #f2f2f2; /* Zebra striping for rows */
}

tr:hover {
    background-color: #0f486d; 
    color: white;
}

.logOut{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
}
.edtPrf{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}
.addTask{
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#delBtn{
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
}
#delBtn:hover{
    background-image: linear-gradient(-225deg, #231557 0%, #44107A 29%, #FF1361 67%, #FFF800 100%);
}
#edtBtn{
    background-image: radial-gradient(circle 248px at center, #16d9e3 0%, #30c7ec 47%, #46aef7 100%);
}
#edtBtn:hover{
    background-image: linear-gradient(-225deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
}
#greenBtn:hover{
    background-image: linear-gradient(to top, #9be15d 0%, #00e3ae 100%);
    /* color: black; */
}
#greenBtn{
    background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
}
.strike-through {
    text-decoration: line-through;
    color: grey; /* Optional: You can change the color of struck tasks */
}
label {
    font-weight: 600;
}